.sidenavbar {
        position: absolute;
        top: 0;
        left: 0;
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        width: 100px;
        height: 1080px;
}
.side-bar-child {
    position: absolute;
    top: calc(50% + 540px);
    left: calc(50% + 50px);
    background-color: rgba(103, 100, 255, 0.5);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    width: 100px;
    height: 1080px;
    transform: rotate(180deg);
    transform-origin: 0 0;
}



